@import "../../scss/config";

.games-section {
  height: 100vh;

  .games-tagline {
    padding: 4rem 1rem 1rem 1rem;
    text-align: center;
  }

  .games-menu {
    min-height: 60vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    align-items: center;
    justify-content: center;

    .gm-card {
      height: 12rem;
      width: 11rem;
      padding: 1rem;
      background-color: rgba($light-beige, 0.8);
      margin: 0.5rem;
      border-radius: 5px;
      color: $dark-grey-brown;

      h3 {
        text-align: center;
        margin: 0.5rem 0;
      }

      p {
        font-weight: 500;
        text-align: center;
      }
    }
  }
}
